@layer utilities;
:root {
  --brand-blue-1: #182063;
  --brand-blue-2: #2f3cbc;
  --brand-blue: var(--brand-blue-1);
  --brand-gold-1: #e1ca9f;
  --brand-gold-2: #d2ae74;
  --brand-gold: var(--brand-gold-1);
  --gradient-angle: 90deg;
  --gradient-blue: linear-gradient(90deg, var(--brand-blue-1), var(--brand-blue-2));
  --gradient-gold: linear-gradient(90deg, var(--brand-gold-1), var(--brand-gold-2));
  --gradient-gold-soft: linear-gradient(
    120deg,
    var(--brand-gold-1),
    var(--brand-gold-2),
    white
  );
  @supports (color: color-mix(in lab, red, red)) {
    --gradient-gold-soft: linear-gradient(
    120deg,
    color-mix(in oklab, var(--brand-gold-1) 55%, white),
    color-mix(in oklab, var(--brand-gold-2) 24%, white),
    white
  );
  }
  --bg: #ffffff;
  --panel: #ffffff;
  --fg: #0b0d13;
  --muted: #4b5563;
  --border: rgba(15, 23, 42, 0.18);
  --border-gold: var(--brand-gold-1);
  @supports (color: color-mix(in lab, red, red)) {
    --border-gold: color-mix(in oklab, var(--brand-gold-1) 30%, transparent);
  }
  --shadow: 0 10px 30px rgba(15, 23, 42, 0.06);
  --shadow-hover: 0 18px 55px rgba(15, 23, 42, 0.12);
  --shadow-soft: 0 6px 18px rgba(15, 23, 42, 0.08);
  --shadow-soft-hover: 0 14px 38px rgba(15, 23, 42, 0.14);
