  --radius-pill: 999px;
  --noise-opacity: 0.055;
  --header-height: 80px;
  --heading-shadow: 0 12px 36px rgba(15, 23, 42, 0.16);
}
body {
  background: var(--bg);
  color: var(--fg);
  font-family: var(--font-sans);
}
h1,
h2,
h3,
.font-display {
  font-family: var(--font-serif);
  letter-spacing: -0.02em;
  text-shadow: var(--heading-shadow);
}
h1,
h2 {
