    background-image: radial-gradient(
      1400px 700px at 80% 80%,
      color-mix(in oklab, var(--brand-blue-1) 16%, transparent),
      transparent 60%
    ),
    radial-gradient(
      900px 500px at 0% 10%,
      color-mix(in oklab, var(--brand-blue-2) 12%, transparent),
      transparent 55%
    ),
    linear-gradient(180deg, rgba(241, 245, 249, 0.55), rgba(255, 255, 255, 1));
  }
