  }
}
.site-header {
  isolation: isolate;
  box-shadow: 0 8px 20px rgba(15, 23, 42, 0.1);
}
.site-header::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  background-image: var(--gradient-blue);
  opacity: 1;
}
